  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
}
.playlist-row__head {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.playlist-row__playlist-name {
  color: var(--3, #fff);
  text-align: center;
  font-family: Uto-600;
  font-size: 20px;
}
.playlist-row__view-button {
  color: var(--light, #ad7955);
  text-align: center;
  font-family: Uto-600;
  font-size: 16px;
  cursor: pointer;
}
.playlist-row__videos {
  width: 100%;
  display: flex;
  flex-direction: row;
  gap: 21.64px;
  overflow-x: auto;
}

